<template>
  <div id="app">
    <div class="con_n">
      <my_nav></my_nav>
    </div>
    <div style="min-height:500px;margin:0 auto">
      <router-view></router-view>
    </div>
    <div class="app-footer">
      <p>咨询电话：00-10001000&nbsp; &nbsp; &nbsp;     咨询QQ：1000 1000 </p>
         <p>机构地址：上海市某某区某某人民路999号（中国）</p>
       <p>&nbsp; &nbsp; &nbsp;  &nbsp; &nbsp; &nbsp;&nbsp; &nbsp; &nbsp;                   多伦多市某某区某某路000号（加拿大）</p>     
    </div>
    <div style="position:fixed;right:30px;bottom:0;width:300px;z-index:50">
      <div style="height:40px;line-height:20px;background:#f00;padding:10px;box-sizing:border-box;color:#fff;font-size:14px" @click="type=!type">
        <span>联系我们</span>
      </div>
      <div :style="{height:type?'0':'420px',padding:type?'0':'20px'}" class="appn">
        <div style="color:#222222;font-size:14px;line-height:20px">
          <p style="color:#222222;margin-bottom:20px">
            您好，如有任何问题，请填写下面的表单。我们将会在第一时间联系您。
          </p>
          <p style="padding:10px 0;">使用QQ和我们在线交谈</p>
          <img src="http://wpa.qq.com/pa?p=2:123456789:51" alt="" style="width:80px;height:25px">
          <div style="font-size:12px;line-height:20px">
            <p>如果我们的QQ不在线，请填写表单。</p>
            <p style="color:#878787;line-height:20px">您的姓名 <span style="color:#f00">*</span>:</p>
            <p style="width:228px;height:32px;border:1px solid #ccc;border-radius:4px;padding-left:10px;font-size:14px;line-height:20px;margin-bottom:10px">
              <input type="text" style="width:100%;height:100%;border-radius:2px;border:none" placeholder="您的姓名（必填项）">
            </p>
            <p style="color:#878787;line-height:20px">邮件地址 <span style="color:#f00">*</span>:</p>
            <p style="width:228px;height:32px;border:1px solid #ccc;border-radius:4px;padding-left:10px;font-size:14px;line-height:20px;margin-bottom:10px">
              <input type="text" style="width:100%;height:100%;border-radius:2px;border:none" placeholder="邮件地址（必填项）">
            </p>
            <p style="color:#878787;line-height:20px">请告诉我们您想咨询的事情  <span style="color:#f00">*</span>:</p>
            <p style="width:228px;height:32px;border:1px solid #ccc;border-radius:4px;padding-left:10px;font-size:14px;line-height:20px;margin-bottom:10px">
              <textarea type="text" style="width:100%;height:100%;border-radius:2px;border:none" placeholder="请告诉我们您想咨询的事情（必填项）"></textarea>
              <div class="clearfix">
                <p style="width:130px;height:32px;border:1px solid #ccc;border-radius:4px;padding-left:10px;font-size:14px;line-height:20px;margin-bottom:10px" class="fl">
                  <input type="text" style="width:100%;height:100%;border-radius:2px;border:none" placeholder="caption">
                </p>
                <p class="fr" style="width:88px;height:33px">
                  <img src="./images/5.png" alt="" style="width:100%;height:100%">
                </p>
              </div>
            <div style="text-align:center">
              <button style="padding:7px 11px;background:#3a99d1;outling:none;border:none;color:#fff">发送</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import my_nav from "./component/my_nav.vue"
export default {
  name: 'app',//有name引入的时候就叫什么，没有就自定义
  data () {
    return {
      type:true
    }
  },
  components:{
    my_nav,
  },
}
</script>

<style>
*{
  margin: 0;
  padding: 0;
  list-style: none;
  /* overflow-y: scroll */
}
.appn{
  height:420px;padding:20px;box-sizing:border-box;overflow-y:scroll;background:#fff;
  transition: 0.35s
}
input{
  outline: none
}
.clearfix::after,.clearfix::before{
  content: "";
  display: table;
}
.clearfix::after{
  clear: both;
}
.clearfix{
  zoom:1
}
.fl{
  float:left
}
.fr{
  float: right;
}
a{
  text-decoration: none
}
.con_n{
  width: 80%;
  margin: 0 auto;
}
.app-footer{
  /* height: 136px; */
  background-color: rgb(76, 76, 76);
  /* width: 50%; */
  margin: 0 auto;
  color: #a9a9a9;
  padding: 20px 0;
  line-height: 32px;
  font-size: 14px;
  text-align: center;
}
p{
  margin: 0 ;
}
</style>



